import React, { Component } from 'react';
import Section from '../components/Section';
import '../styles/sections.css';

class Sections extends Component {
	constructor() {
		super();
		this.state = {
			sections: []
		}
	}

	componentDidMount(){
		fetch("https://gist.githubusercontent.com/hifebriansyah/5f62028cf655a6b5af592986057d88ef/raw/sections-"+this.props.match.params.id+".json")
			.then(response => {
				return response.json();
			})
			.then(result => {
				let sections = result.data.map((section, i) => {  
					return (
						<Section 
							name={section.name}
							key={i}
							chapters={section.chapters} />
					)                 
				});

				this.setState({sections})
			});
	}

	render() {
	    return(
	        <div className="sections page">
	    		{this.state.sections}
	        </div>
	    )
	}
}

export default Sections;